import { NamespacedEnum } from './enum';

// Filter by hostname (in order)
//
// 1. PROD > contains student.com
// 2. UAT > ends with -uat.dandythrust.com
// 3. STAGE > ends with .dandythrust.com
// 4. DEV > everything else

let environment;

export const environments = new NamespacedEnum('ENVIRONMENT', [
  'PROD',
  'STAGE',
  'UAT',
  'UAT1',
  'UAT2',
  'UAT3',
  'DEV',
]);

export function clearCache() {
  environment = null;
}

const isUat = () => {
  if (window.location && window.location.hostname) {
    const { hostname } = window.location;
    return hostname.includes('uat');
  }

  return false;
};

export function getEnvironmentUAT() {
  const { hostname } = window.location;
  if (hostname.includes('uat1')) {
    return environments.UAT1;
  }
  if (hostname.includes('uat2')) {
    return environments.UAT2;
  }
  if (hostname.includes('uat3')) {
    return environments.UAT3;
  }
  return 'unknow';
}

export function getCookieDomain() {
  // NOTE: This only works client side, but will fail gracefully
  let domain = '';

  if (hostnameEndsWith('student.com')) {
    domain = 'student.com';
  } else if (hostnameEndsWith('xuelujia.xyz')) {
    domain = 'xuelujia.xyz';
  } else if (hostnameEndsWith('dandythrust.com')) {
    domain = 'dandythrust.com';
  }

  return domain;
}

export function getGraphQlDomain() {
  const env = getEnvironment();
  if (env === environments.PROD) {
    return '//gateway.student.com/graphql';
  } else if (env === environments.STAGE) {
    return '//gateway.dandythrust.com/graphql';
  } else if (env === environments.UAT1 ||
    env === environments.UAT2 ||
    env === environments.UAT3
  ) {
    return `//gateway-${env}.dandythrust.com/graphql`;
  }
  return '//gateway.dandythrust.com/graphql';
}

function hostnameEndsWith(string) {
  if (typeof window === 'undefined') {
    return false;
  }

  const hostname = window.location.hostname;
  return hostname.substring(hostname.length - string.length) === string;
}

export function isChinaSubdomain() {
  if (window.location.hostname.match(/.*cn.*\.com/i)) {
    return true;
  }

  return false;
}

export default function getEnvironment() {
  if (environment) {
    return environment;
  }

  if (typeof window === 'undefined') {
    environment = environments.PROD;
  } else if (hostnameEndsWith('student.com')) {
    environment = environments.PROD;
  } else if (hostnameEndsWith('dandythrust.com') && isUat()) {
    environment = getEnvironmentUAT();
  } else if (hostnameEndsWith('dandythrust.com') && !isUat()) {
    environment = environments.STAGE;
  } else {
    environment = environments.DEV;
  }

  return environment;
}



